d-color;\n  \n  //breakpoints for reasonable resizing of fonts on other devices\n  @include mixins.respond(tab-land){  // width < 1200\n    font-size: 56.25%;\n  }\n  @include mixins.respond(tab-port){  // width < 900?\n    font-size: 50%;\n  }\n  @include mixins.respond(phone){\n    font-size: 40%;\n    letter-spacing: 0.01rem;\n  }\n  \n  @include mixins.respond(desktop){\n    font-size: 66%;\n  }\n  @include mixins.respond(big-desk){  // width > 1800\n    font-size: 75%;\n  }\n}\n\nli{\n  list-style: none;\n}\n\n.app-div{\n  display: flex;\n  flex-flow: column nowrap;\n  align-items: stretch;\n\n}\n\n","@use '../abstracts/vars';\n\n// generalized responsive design breakpoints\n@mixin respond($breakpoint){\n  @if $breakpoint == small-phone {\n    @media screen and (max-width: 25em) { @content };\n  }\n  @if $breakpoint == phone {\n    @media screen and (max-width: 37.5em) { @content }; // 600px\n  }\n  @if $breakpoint == tab-port {\n    @media screen and (max-width: 56.25em){ @content }; // 900px | 1em = 38.4px\n  }\n  @if $breakpoint == tab-land {\n    @media screen and (max-width: 75em) { @content }; // up to 750px? 1200px | 1em = 48px\n  }\n  @if $breakpoint == desktop { // 760 - 1120?\n    @media (min-width: 76em) and (max-width: 112em){ @content };\n  }\n  @if $breakpoint == m-desktop { //1130 - 1240?\n    @media (min-width: 113em ) and (max-width: 124em){ @content };\n  }\n  @if $breakpoint == big-desk {\n    @media screen and (min-width: 125em) { @content }; // 1800px?\n  }\n}\n\n@mixin fontShadow(){\n  text-shadow: \n    0.3rem 0.3rem vars.$button-font-shadow, \n    -1px 0 1px vars.$button-font-outline, \n    0 -1px 1px vars.$button-font-outline;\n}\n\n@mixin newHeadingShadow(){\n  color: white;\n  text-shadow: 0.06em 0em 0.03em vars.$goth-lavender, 0.1em 0 vars.$potential-blue;\n}\n\n@mixin desktopPseudoDivider(){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1.5rem;\n\n  border: 0.3rem solid vars.$portland-red;\n  height: 0.2rem;\n  width: 88%;\n  margin: -3rem -44%;\n}\n\n@mixin mobilePseudoDivider(){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1rem;\n\n  border: 0.4rem solid vars.$portland-red;\n  height: 0.3rem;\n  width: 90%;\n  margin: -2rem -45%;\n}\n\n@mixin singleLinePseudoDivider($width){\n  content: '';\n  position: absolute;\n  left: 50%;\n  top: 1rem;\n\n  border-top: 0.4rem solid vars.$portland-red;  \n  width: vars.$width or 90%;\n  margin: -2rem (-vars.$width/2 or -45%);\n}\n\n@mixin sectionBorderTop(){\n  border-top: 3px solid vars.$portland-red;\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.nav-hat-wrapper{\n  position: fixed;\n  // width: 100vw;\n  width: 100%;\n  top: 0;\n  overflow: hidden;\n  z-index: 2;\n\n  button {\n    border: none;\n    cursor: pointer;\n    background: none;\n    text-align: center;\n\n    font-size: 2.6em;\n   \n    @include mixins.newHeadingShadow();\n\n  }\n\n  button:hover {\n    color: vars.$light-font-hover;\n  }\n\n  button:active{\n    color: vars.$portland-off-white;\n    transform: translateY(0.1rem);\n  }\n\n  nav{\n    .logo{\n      position: relative;\n      height: auto;\n      width: auto;\n      margin: 0 vars.$desktop-side-margin/2 0 vars.$desktop-side-margin;\n  \n      @include mixins.respond(phone){\n        height: vars.$phone-nav-logo-height;\n        padding: 0;\n        margin-right: 0;\n        margin-left: vars.$phone-side-margin;\n      }\n    }\n\n    .button-group{\n      position: relative;\n      margin: 0 vars.$desktop-side-margin 0 vars.$desktop-side-margin;\n      width: 100%;\n\n      display: flex;\n      flex-flow: row;\n      justify-content: space-between;\n  \n      font-weight: 700;\n      font-family: inherit;\n    }\n  }  \n}\nnav{ z-index: 1;}\nnav div{ z-index: 2;}\n\nbutton.button-current-page{\n  border-bottom: 3px solid vars.$portland-red;\n  box-sizing: content-box;\n  margin-bottom: -3px;\n}\n","$light-font-visited: #dddddd;\n$light-font-hover: #ffffff;\n\n$alt-dark-font: #201713;\n$dark-font: #2d2d2d;\n$soft-black: #424242;\n$button-font-shadow: #787878;\n$button-font-size: 2.5rem;\n$button-font-outline: #787878e6;\n\n$bright-brown: #9e4334;\n$less-bright-brown: #a0170f;\n$medium-brown: #94241e;\n$brown-brown: #6a2008;\n\n$menu-background-color: #f7f3e3;\n\n$portland-beige: #babd9b;\n$portland-off-white: #efeeec;\n$portland-red: #bd141b;\n$portland-red-alpha: #bd141ad7;\n\n//////////////////////////////////\n/////// NEW COLOR SCHEMES\n$portland-mustard: #bdb714;\n$dull-orange: #bd6314;\n\n$portland-pink: #bd146e;\n$portland-dull-red: #a33236;\n$potential-blue: #146ebd;\n\n$maybe-green: #70bd14;\n\n/////////////////////// \n//// try this \n// #bd141b, #f7f3e3, #ecf0f1, #2b2118, #6a5d7b\n$nu-beige: #f7f3e3;\n$fresh-walls: #ecf0f1;\n$brown-q: #2b2118; \n$faded-lavender: #6a5d7b;\n$goth-lavender: #625671;\n\n// PHONE STYLING\n$phone-nav-height: 10rem;\n$phone-nav-fontsize: 2.2rem;\n$phone-nav-logo-height: 7.5rem;\n\n$phone-item-name-fontsize: 2.2rem; \n$phone-item-notes-fontsize: 1.8rem;\n$phone-item-price-fontsize: 2.1rem;\n$phone-item-price-dot: 0.7rem;\n$phone-item-logo-size: 2.2rem;\n\n$phone-section-fontsize: 3.5rem;\n$phone-side-margin: 1.6rem;\n\n// TABLET STYLING\n\n$mobile-nav-height: 9rem;\n$mobile-nav-fontsize: 3.6rem;\n\n$mobile-section-fontsize: 4rem;\n$mobile-sub-section-fontsize: 3rem;\n\n$mobile-item-name-fontsize: 2rem;\n$mobile-item-notes-fontsize: 1.7rem;\n$mobile-item-price-fontsize: 1.9rem;\n$mobile-item-price-dot: 0.6rem;\n$mobile-side-margin: 2.5rem;\n\n$mobile-menu-width: 100%;\n\n// DESKTOP STYLING\n\n$desktop-pad-top: 4rem;\n$desktop-nav-height: 15rem;\n$desktop-nav-element-height: 10rem;\n$desktop-nav-logo-height: 8rem;\n\n$desktop-section-fontsize: 2.2rem;\n$desktop-sub-section-fontsize: 1.5rem;\n\n$desktop-item-base-fontsize: 1.5rem;\n$desktop-item-name-fontsize: 1.8rem;\n$desktop-item-notes-fontsize: 1.4rem;\n$desktop-item-price-fontsize: 1.6rem;\n$desktop-item-price-dot: 0.5rem;\n$desktop-item-logo-size: 1.4rem;\n\n$desktop-navhat-side-pad: 3rem;\n$desktop-side-margin: 20rem;\n// $desktop-menu-width: 76vw;\n\n$desktop-secondary-nav-top: 12rem;\n$desktop-secondary-nav-height: 11rem;\n","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.full-nav{\n  // position: fixed;\n  box-sizing: content-box;\n  top: 0;\n\n  width: inherit;\n  \n  height: vars.$desktop-nav-height;\n  display: flex;\n  flex-flow: row;\n  justify-content: space-between;\n  align-items: center;\n\n  @include mixins.respond(tab-port){\n    display: none;\n  }\n}\n\n.bknd-img-wrapper{\n  position: absolute;\n  width: 100%;\n  height: vars.$desktop-nav-height;\n  overflow: hidden;\n\n  @include mixins.respond(big-desk){\n    background-color: purple;\n  }\n\n  img{\n    object-fit: cover;\n    width: 110%;\n    filter: brightness(45%);\n    transform: translate(-10rem, -52rem) rotate(-12deg);\n\n    @include mixins.respond(desktop){\n      transform: translate(-10rem, -58rem) rotate(-12deg);\n    }\n  }\n}\n\n.secondary-nav-scroll-target{\n  position: absolute;\n  top: vars.$desktop-secondary-nav-top;\n\n  @include mixins.respond(tab-port){\n    display: none;\n  }\n}\n\n.secondary-nav{\n  position: fixed;\n  top: -(vars.$desktop-secondary-nav-height) ;\n\n  display: flex;\n  flex-flow: row;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  height: vars.$desktop-secondary-nav-height;\n  background-color: vars.$menu-background-color;\n  transition: transform 0.3s;\n\n  @include mixins.respond(tab-port){\n    display: none;\n  }\n\n  .bknd-img-wrapper{\n    height: vars.$desktop-secondary-nav-height;\n\n    & img{\n      transform: translate(0rem, -17rem);\n    }\n  }\n}\n\n.secondary-nav-open{\n  transform: translateY(100%);\n  @include mixins.respond(tab-port){\n    display: none;\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.nav-hat-wrapper .mobile-nav{\n  position: fixed;\n  top: 0;\n  width: 100vw;\n  height: vars.$mobile-nav-height;\n\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: stretch;\n  align-items: center;\n  \n  background-color: vars.$alt-dark-font;\n\n  &__logo.logo{\n    display: inline-block;\n    height: vars.$mobile-nav-height - 1.6rem;\n    margin: 0 0 0 vars.$mobile-side-margin;\n\n    background-color: vars.$alt-dark-font;\n\n    @include mixins.respond(phone){\n      height: vars.$phone-nav-logo-height;\n      margin: 0.5rem 0 0.5rem vars.$phone-side-margin;\n    }\n\n    @include mixins.respond(small-phone){\n      height: vars.$phone-nav-logo-height - 0.8rem;\n      margin: 0.5rem 0 0.5rem (vars.$phone-side-margin)-0.5rem;\n    }\n\n    img{\n      height: 100%;\n    }\n  } \n\n  &__button-group.button-group{\n    display: flex;\n    flex-flow: row nowrap;\n    align-items: center;\n    justify-content: space-between;\n    width: 100%;\n    height: 100%;\n\n    margin: 0;\n\n    @include mixins.respond(tab-land){\n      margin: 0 (vars.$mobile-side-margin + 1rem );\n    }\n    \n    @include mixins.respond(phone){\n      margin-right: vars.$phone-side-margin;\n      margin-left: vars.$phone-side-margin*2;\n    }\n\n    @include mixins.respond(small-phone){\n      margin-left: vars.$phone-side-margin;\n      margin-right: vars.$phone-side-margin - 0.5rem;\n    }\n    \n    button{\n      font-size: 2.6em;\n      background-color: vars.$alt-dark-font;\n\n      // @include newHeadingShadow();\n      \n      @include mixins.respond(phone){\n        margin-left: 0.5rem;\n        margin-top: 0.6rem;\n\n        font-size: 2.4em;\n      }\n\n      @include mixins.respond(small-phone){\n        font-size: 2em;\n      }\n    }\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.footer{\n  position: relative;\n  top: vars.$desktop-nav-height; //if this changes, content needs adjusting too\n  width: 76vw;\n  margin: 0 auto;\n\n  padding: 3rem;\n  display: flex;\n  flex-flow: column;\n  align-items: flex-start;\n\n  justify-content: end;\n\n  font-size: 1.3rem;\n\n  @include mixins.respond(tab-port){\n    // width: 100%;\n    padding: 2rem;\n\n    flex-flow: column;\n    align-items: stretch;\n    justify-content: space-between;\n  }\n\n  & a {\n    color: vars.$brown-q;\n    text-decoration: none;\n  }\n  & a:link {\n    color: vars.$brown-q;\n  }\n  & a:visited {\n    color: vars.$brown-q;\n  }\n  & a:hover {\n    color: vars.$brown-q;\n  }\n  & a:active {\n    color: vars.$brown-q;\n  }\n  & a:focus {\n    color: vars.$brown-q;\n  }\n\n  & div {\n    margin-top: 1rem;\n  }\n\n  &-address {\n    font-size: 1.3em;\n  }\n\n  &-social {\n\n    display: flex;\n    flex-flow: row;\n    align-items: center;\n\n    @include mixins.respond(tab-port){\n      align-items: flex-start;\n    }\n\n\n    &-link {\n\n      margin-right: 2rem;\n  \n      img {\n        height: 3rem;\n      }\n    } \n  }\n\n  &-phonenumber {\n    margin-bottom: 1rem;\n    font-size: 1.5em;\n  }\n\n  &-author-info {\n    margin-top: 1rem;\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n.cocktail-section{\n  grid-column: 1;\n  grid-row: 3;\n\n  display: grid;\n  grid-template-columns: 3fr 4fr 3fr;\n  grid-template-rows: 7rem auto;\n\n  border-top: 3px solid vars.$portland-red;\n\n  font-size: 2.6rem;\n\n  @include mixins.respond(tab-port){\n    grid-column: 1 / span 7;\n    grid-row: 2 / span 2;\n\n    width: 99%;\n  }\n\n  @include mixins.respond(phone){\n    // font-size: 1.2em;\n  }\n\n  &-title{\n    grid-column: 2;\n    grid-row: 1;\n    justify-self: center;\n\n\n    color: vars.$portland-red;\n    font-size: 1.2em;\n  }\n\n  &-list.section-list{\n\n    grid-column: 1 / span 3;\n    grid-row: 2;\n\n    font-size: 0.8em;\n\n    @include mixins.respond(phone){\n      padding: 1rem 2rem;\n    }\n\n    .cocktail-item{\n      display: grid;\n      grid-template-columns: 8fr 2fr;\n      grid-template-rows: auto auto;\n\n\n      margin: 0.5rem 0;\n\n      &-name{\n        grid-column: 1;\n        grid-row: 1;\n\n        font-size: 0.9em;\n\n        @include mixins.respond(tab-port) {\n          font-size: 1.3em;\n        }\n      }\n\n      &-price{\n        grid-column: 2;\n        grid-row: 1;\n        justify-self: end;\n\n        font-size: 0.77em; //aim for 13px\n\n        @include mixins.respond(tab-port) {\n          font-size: 1em;\n        }\n      }\n\n      &-notes{\n        grid-column: 1 / span 2;\n        grid-row: 2;\n\n        font-size: 0.7em;\n        font-style: italic;\n\n        @include mixins.respond(tab-port) {\n          font-size: 0.95em;\n        }\n      }\n    }\n  }\n}","@use '../abstracts/vars';\n@use '../abstracts/mixins';\n\n////////////////////////////////////////////////\n//////////// FOOD SECTION\n.food-section {\n  \n  position: relative;\n  margin-bottom: 1rem;\n\n  min-width: 45%;\n  \n  display: flex;\n  flex-flow: column;\n\n  border-top: 3px solid vars.$portland-red;\n  \n  @include mixins.respond(tab-port){\n    margin-bottom: 1.5rem;\n    align-items: stretch;\n  }\n\n  .section__title {\n    align-self: flex-start;\n    text-transform: capitalize;\n    font-weight: 700;\n    font-size: 0.95em;\n    text-align: center;\n    color: vars.$portland-red;\n\n    margin-top: 2rem;\n    padding-left: 1rem;\n\n    @include mixins.respond(big-desk){\n      font-size: 1.2em;\n    }\n\n    @include mixins.respond(phone){\n      font-size: 1.2em;\n    }\n  }\n\n  .section__list{\n\n    display: flex;\n    flex-flow: column wrap;\n    justify-content: start;\n    max-height: 30rem;\n    max-width: 45%;\n\n    padding: 1rem;\n\n    @include mixins.respond(tab-port){\n      display: grid;\n      grid-template-columns: 40vw 40vw;\n      justify-content: space-around;\n      align-items: stretch;\n      \n\n      max-height: unset;\n      max-width: unset;\n      text-align: start;\n    }\n    \n    @include mixins.respond(phone){\n      display: flex;\n      flex-flow: column nowrap;\n      align-items: stretch;\n    }\n  }\n}\n\n\n\n//////////////////////////////////// \n//////